import React from "react";
import Slider from "../Slider/Slider";
import s from "./PremiersFilms.module.scss";
import classNames from "classnames";
export default function PremiersFilms({ title, movies }) {
  return (
    <section className={s.premiers}>
      <h1 className={classNames(s.premiers__title, "title")}>{title}</h1>
      <Slider movies={movies}></Slider>
    </section>
  );
}
