import React from "react";
import s from "./Genres.module.scss";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/navigation";
import { Navigation } from "swiper/modules";
import { Link } from "react-router-dom";
import classNames from "classnames";
import { LazyLoadImage } from "react-lazy-load-image-component";
export default function Genres({ title }) {
  return (
    <section className={s.genres}>
      <h2 className={classNames(s.genres__title, "title")}>{title}</h2>
      <Swiper
        breakpoints={{
          400: {
            slidesPerView: 6.5,
          },
        }}
        modules={[Navigation]}
        spaceBetween={7}
        navigation
        slidesPerView={4.5}
        className={s.slider}>
        <SwiperSlide className={s.slide}>
          <Link to={"/films/comedy"}>
            <article className={s.genres__card}>
              <picture>
                <source srcSet={require("../../images/banners1.png")} />
                <LazyLoadImage src={require("../../images/banners1.png")} alt="banner" loading="lazy" />
              </picture>
            </article>
          </Link>
        </SwiperSlide>
        <SwiperSlide className={s.slide}>
          <Link to={"/films/melodrama"}>
            <article className={s.genres__card}>
              <picture>
                <source srcSet={require("../../images/banners2.png")} />
                <LazyLoadImage src={require("../../images/banners2.png")} alt="banner" loading="lazy" />
              </picture>
            </article>
          </Link>
        </SwiperSlide>
        <SwiperSlide className={s.slide}>
          <Link to={"/films/boevik"}>
            <article className={s.genres__card}>
              <picture>
                <source srcSet={require("../../images/banners3.png")} />
                <LazyLoadImage src={require("../../images/banners3.png")} alt="banner" loading="lazy" />
              </picture>
            </article>
          </Link>
        </SwiperSlide>
        <SwiperSlide className={s.slide}>
          <Link to={"/films/semejnyj"}>
            <article className={s.genres__card}>
              <picture>
                <source srcSet={require("../../images/banners4.png")} />
                <LazyLoadImage src={require("../../images/banners4.png")} alt="banners" loading="lazy" />
              </picture>
            </article>
          </Link>
        </SwiperSlide>
        <SwiperSlide className={s.slide}>
          <Link to={"/films/uzhasy"}>
            <article className={s.genres__card}>
              <picture>
                <source srcSet={require("../../images/banners5.png")} />
                <LazyLoadImage src={require("../../images/banners5.png")} alt="banners" loading="lazy" />
              </picture>
            </article>
          </Link>
        </SwiperSlide>
        <SwiperSlide className={s.slide}>
          <Link to={"/films/fantastika"}>
            <article className={s.genres__card}>
              <picture>
                <source srcSet={require("../../images/banners6.png")} />
                <LazyLoadImage src={require("../../images/banners6.png")} alt="banners" loading="lazy" />
              </picture>
            </article>
          </Link>
        </SwiperSlide>
        <SwiperSlide className={s.slide}>
          <Link to={"/films/drama"}>
            <article className={s.genres__card}>
              <picture>
                <source srcSet={require("../../images/banners7.png")} />
                <LazyLoadImage src={require("../../images/banners7.png")} alt="banners" loading="lazy" />
              </picture>
            </article>
          </Link>
        </SwiperSlide>
      </Swiper>
    </section>
  );
}
