import React from "react";
import Slider from "../Slider/Slider";
import s from "./Top100.module.scss";
import classNames from "classnames";
export default function Top100({ title, topMovies }) {
  return (
    <section className={s.top}>
      <h1 className={classNames(s.top__title, "title")}>{title}</h1>
      <Slider movies={topMovies}></Slider>
    </section>
  );
}
